import React from 'react';
import style from './Nav.module.scss';
import { Link } from "react-scroll";

function Nav() {
    return (
        <div className={style.nav}>
            <Link
                className={style.navLink}
                activeClass={style.active}
                to="main"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
            >Main</Link>
            <Link
                className={style.navLink}
                activeClass={style.active}
                to="skills"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
            >Skills</Link>
            <Link
                className={style.navLink}
                activeClass={style.active}
                to="my-jobs"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
            >Projects</Link>
            <Link
                className={style.navLink}
                activeClass={style.active}
                to="contacts"
                spy={true}
                smooth={true}
                offset={0}
                duration={500}
            >Contacts</Link>
        </div>
    );
}

export default Nav;
